import React from 'react';
import TimePicker from 'react-time-picker';
import './WorkSettings.css'; 

function WorkSettings({
  requiredWorkingTime,
  setRequiredWorkingTime,
  coreTimeStart,
  setCoreTimeStart,
  coreTimeEnd,
  setCoreTimeEnd,
  lunchBreakTime,
  setLunchBreakTime,
  fixedStartTime,
  onFixedStartTimeChange,
  togglePopup
}) {
  return (
    <div className="work-settings-overlay">
      <div className="work-settings">
        <h2></h2>
        <div className="work-settings-time-pickers">
          {/*    */}
          <div className="work-settings-row">
            <label htmlFor="requiredWorkingTime" className="work-settings-label">  </label>
            <div className="time-picker">
                <TimePicker
                    id="requiredWorkingTime"
                    onChange={setRequiredWorkingTime}
                    value={requiredWorkingTime}
                    format="HH:mm"
                    clearIcon={null}
                    maxDetail="minute"
                    disableClock
                />
            </div>
            
          </div>

          {/*   */}
          <div className="work-settings-row">
            <label htmlFor="coreTimeStart" className="work-settings-label"> </label>
            <div className="time-picker">
                <TimePicker
                id="coreTimeStart"
                onChange={setCoreTimeStart}
                value={coreTimeStart}
                format="HH:mm"
                clearIcon={null}
                maxDetail="minute"
                disableClock
                />
            </div>
            
          </div>

          {/*   */}
          <div className="work-settings-row">
            <label htmlFor="coreTimeEnd" className="work-settings-label"> </label>
            <div className="time-picker">
                <TimePicker
                id="coreTimeEnd"
                onChange={setCoreTimeEnd}
                value={coreTimeEnd}
                format="HH:mm"
                clearIcon={null}
                maxDetail="minute"
                disableClock
                />
            </div>
            
          </div>

          {/*    */}
          <div className="work-settings-row">
            <label htmlFor="lunchBreakTime" className="work-settings-label">  </label>
            <div className="time-picker">
                <TimePicker
                id="lunchBreakTime"
                onChange={setLunchBreakTime}
                value={lunchBreakTime}
                format="HH:mm"
                clearIcon={null}
                maxDetail="minute"
                disableClock
                />
            </div>

            
          </div>

          {/*    ( ) */}
          <div className="work-settings-row">
            <label htmlFor="fixedStartTime" className="work-settings-label">
                
            </label>
            <div className="time-picker">
              <TimePicker
                id="fixedStartTime"
                onChange={onFixedStartTimeChange}
                value={fixedStartTime}
                format="HH:mm"
                clearIcon={null}
                maxDetail="minute"
                disableClock
              />
            </div>
          </div>
        </div>


        <button
        onClick={togglePopup}
        className="work-settings-close-btn">
          
        </button>
      </div>
    </div>

  );
}

export default WorkSettings;
